import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface TicketRequestParams {
  from_city_id?: string, //-  ,    ()
  to_city_id?: string, //-  ,    ()
  date_start?: string, //-    (  YYYY-MM-DD;  2030-03-01)
  date_end?: string, //-    (  YYYY-MM-DD;  2030-03-01)
  date_start_arrival?: string, //-    (  YYYY-MM-DD;  2030-03-01)
  date_end_arrival?: string, //-    (  YYYY-MM-DD;  2030-03-01)
  have_first_class?: boolean, //-  (true/false)
  have_second_class?: boolean, //-  (true/false)
  have_third_class?: boolean, //-  (true/false)
  have_fourth_class?: boolean, //-   (true/false)
  have_wifi?: boolean, //-  WiFi (true/false)
  have_air_conditioning?: boolean, //-   (true/false)
  have_express?: boolean, //-  (true/false)
  price_from?: number, //-  
  price_to?: number, //-  
  start_departure_hour_from?: number, //-    ()
  start_departure_hour_to?: number, //-    ()
  start_arrival_hour_from?: number, //-    ()
  start_arrival_hour_to?: number, //-    ()
  end_departure_hour_from?: number, //-     ()
  end_departure_hour_to?: number, //-     ()
  end_arrival_hour_from?: number, //-     (    date_end)
  end_arrival_hour_to?: number, //-     (    date_end)
  limit?: number, //-    
  offset?: number, //-  ,     
  sort?: 'date' | 'price' | 'duration', //-   (date, price, duration)
}

interface stateInterface {
  isFirstRequest: boolean;
  loadingStatus: string | null;
  params: TicketRequestParams;
}

const initialState: stateInterface = {
  loadingStatus: null,
  isFirstRequest: false,
  params: {
    offset: 0
  }
}

interface updateRequestAction {
  key: string,
  value: number | string | boolean,
}

export const ticketSearchRequestSlises = createSlice({
  name: 'ticketSearchRequest',
  initialState,
  reducers: {
    updateRequestParameter: (state, action: PayloadAction<updateRequestAction>) => {
      const key = action.payload.key;
      const value = action.payload.value;
      if (!value && value !== 0) {
        delete state.params[key as keyof TicketRequestParams]
        state.params = { ...state.params };
        return state;
      } else {
        state.params = { ...state.params, [key]: value };
        return state;
      }
    },
    updateLoadingStatus: (state, action: PayloadAction<string | null>) => {
      const status = action.payload;
      state.loadingStatus = status;
      return state;
    },
    isFirstRequestUpdate: (state, action: PayloadAction<boolean>) => {
      state.isFirstRequest = action.payload;
      return state;
    }
  },
})

export const { updateRequestParameter, updateLoadingStatus, isFirstRequestUpdate } = ticketSearchRequestSlises.actions;

export default ticketSearchRequestSlises.reducer;