import * as Yup from "yup";

export const validationSchema = Yup.object().shape({
  first_name: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  last_name: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  patronymic: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  phone: Yup.string()
  .required()
  .matches(/^(?:\+7|8)\d{10}$/),
  email: Yup.string()
    .required(' e-mail')
    .email(' e-mail'),
  payment_method: Yup.string()
    .matches(/^(cash|online)$/)
    .required(),
})