import * as Yup from 'yup';

export const passportValidationSchema = Yup.object().shape({
  first_name: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  last_name: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  patronymic: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  gender: Yup.boolean(),
  birthday: Yup.date().nullable().min(new Date(1900, 0, 1)).required('  '),
  document_type: Yup.string().required(),
  document_serial: Yup.string()
    .matches(/^\d{4}$/, '     4 ')
    .required('  '),
  document_number: Yup.string()
  .matches(/^\d{6}$/, '     6 ')
  .required('    '),
})

export const birthdayCertificateValidationSchema = Yup.object().shape({
  first_name: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  last_name: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  patronymic: Yup.string()
    .min(2, '  ')
    .max(50, '  ')
    .required(' '),
  gender: Yup.boolean(),
  birthday: Yup.date().nullable().min(new Date(1900, 0, 1)).required('  '),
  document_type: Yup.string().required(),
  birthday_certificate: Yup.string()
  .matches(/^[IVX]{2}-?[-]{2}-?\d{6}$/, '       XV--123456')
  .required('    ')
})